<template>
    <div>
       <el-breadcrumb >
        <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
        <el-breadcrumb-item>订单管理</el-breadcrumb-item>
        <el-breadcrumb-item>订单列表</el-breadcrumb-item>
        </el-breadcrumb>
        <el-card style="margin-top:20px">
            <el-row>
                <el-col :span="8">
                     <el-input placeholder="请输入内容" v-model.trim="querinfo.query" clearable @clear="getOdersList">
                        <el-button slot="append" icon="el-icon-search" @click="getOdersList"></el-button>
                    </el-input>
                </el-col>
            </el-row>
            <el-table :data="orderList" border stripe>
                <el-table-column type="index"></el-table-column>
                <el-table-column label="订单号" prop="order_number"></el-table-column>
                <el-table-column label="订单价格" prop="order_price"></el-table-column>
                 <el-table-column label="是否付款" prop="pay_status">
                     <template slot-scope="scope">
                         <el-tag type="success" v-if="scope.row.pay_status == 1">已付款</el-tag>
                         <el-tag type="danger" v-else>未付款</el-tag>
                     </template>
                 </el-table-column>
                   <el-table-column label="是否发货" prop="is_send">

                   </el-table-column>
                     <el-table-column label="下单时间" prop="create_time">
                         <template slot-scope="scope">
                                {{scope.row.create_time | dataFormat}}
                         </template>
                     </el-table-column>
                  <el-table-column label="操作">
                      <template slot-scope="scope">
                          <el-button type="primary">编辑</el-button>
                           <el-button @click="showProgressBox">物流进度</el-button>
                      </template>
                  </el-table-column>
            </el-table>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="querinfo.pagenum"
                :page-sizes="[10, 20, 30, 40]"
                :page-size="querinfo.pagesize"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
        </el-card>
          <el-dialog
            title="物流进度"
            :visible.sync="ProgressVisible"
            width="50%"
            @close="ProgressClose"
            >
            <el-timeline>
                <el-timeline-item v-for="(v,i) in progressInfo" :key="i" :timestamp="v.time">{{v.context}}</el-timeline-item>
            </el-timeline>
            <span slot="footer" class="dialog-footer">
                <el-button @click="ProgressVisible = false">取 消</el-button>
                <el-button type="primary" @click="ProgressParams">确 定</el-button>
            </span>
        </el-dialog>
    </div>
</template>
<script>
export default {

    data() {
        return {
            ProgressVisible:false,
            querinfo:{
                query:"",
                pagenum:1,
                pagesize:10
            },
            orderList:[],
            total:0,
            progressInfo:[],
        };
    },
    created(){
        this.getOdersList()
    },
    mounted() {
        
    },

    methods: {
        async getOdersList(){
            const{data:res} = await this.$http.get('orders',{params:this.querinfo});
             if(res.meta.status != 200) return this.$message.error('获取订单列表失败')
             console.log(res.data)
             this.orderList = res.data.goods;
             this.total = res.data.total;
        },
        handleSizeChange(newcer){
            this.querinfo.pagesize = newcer;
            this.getOdersList()
        },
        handleCurrentChange(newcer){
            this.querinfo.pagenum = newcer;
            this.getOdersList()
        },
     async   showProgressBox(){
        this.ProgressVisible = true;
        this.progressInfo = [{
            time:'12:00',
            context:'XX省XX市XX区（县）XX小区XX单元XX号'
        },
        {
            time:'1:00',
            context:'XX省XX市XX区（县）XX小区XX单元XX号'
        }
        ]
        //  const {data:res} = await this.$http.get('/kuaidi/804909574412544580');
        //  if(res.meta.status != 200) return this.$message.error('获取物流进度失败')
           
        },
        ProgressClose(){

        },
        ProgressParams(){

        }
    },
};
</script>
<style>
    @import '../../plugins/timeline/timeline.css';
    @import '../../plugins/timeline-item/timeline-item.css'
</style>